import PartialDischargeEcharts from '../vibration/PartialDischargeEcharts.vue';
import {
  vibrate_check,
  vibrate_chart,
  devicetype,
  devicename,
  deviceattr,

} from '../../utils/service';
export default {
  components: {
    PartialDischargeEcharts
  },
  data() {
    return {
      devicename: '', // 设备名称
      devicetype: '', // 设备类型
      deviceattr: '', // 设备属性
      timedata: '',
      sdevicename: '', // 确认设备名称
      sdevicetype: '', // 确认设备类型
      sdeviceattr: '', // 确认设备属性
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      datavalue1: '',
      gettypevalue: '',
      dataNameValue: [{
          title: 'X轴加速度峰值(m/s²)',
          value: 'accx'
        }, {
          title: 'Y轴加速度峰值(m/s²)',
          value: 'accy'
        }, {
          title: 'Z轴加速度峰值(m/s²)',
          value: 'accz'
        }, {
          title: 'X轴加速度有效值(m/s²)',
          value: 'evax'
        }, {
          title: 'Y轴加速度有效值(m/s²)',
          value: 'evay'
        }, {
          title: 'Z轴加速度有效值(m/s²)',
          value: 'evaz'
        }, {
          title: 'X轴速度峰值(mm/s)',
          value: 'pgvx'
        }, {
          title: 'Y轴速度峰值(mm/s)',
          value: 'pgvy'
        }, {
          title: 'Z轴速度峰值(mm/s)',
          value: 'pgvz'
        }, {
          title: 'X轴速度有效值(mm/s)',
          value: 'velx'
        }, {
          title: 'Y轴速度有效值(mm/s)',
          value: 'vely'
        }, {
          title: 'Z轴速度有效值(mm/s)',
          value: 'velz'
        }, {
          title: 'X轴位移峰值(µm)',
          value: 'disx'
        }, {
          title: 'Y轴位移峰值(µm)',
          value: 'disy'
        }, {
          title: 'Z轴位移峰值(µm)',
          value: 'disz'
        },
        {
          title: 'X轴位移有效值(µm)',
          value: 'disvvx'
        }, {
          title: 'Y轴位移有效值(µm)',
          value: 'disvvy'
        }, {
          title: 'Z轴位移有效值(µm)',
          value: 'disvvz'
        },

        {
          title: '温度（℃）',
          value: 'temp'
        }, {
          title: '电池剩余电量（℃）',
          value: 'batteryrp'
        },
      ],
      datalist: '',
      devicename: '',
      deviceattr: '',
      devicetype: '021086',
      edata: ''

    };
  },
  mounted() {
    this.sTblist()
    this.getdevicename()
    this.getdeviceattr()
  },
  methods: {
    sTblist() {
      vibrate_check({}, res => {
        this.datalist = res.data
      });
    },
    topsure() {
      var startdate = ''
      var enddate = ''
      for (let index = 0; index < this.timedata.length; index++) {
        startdate = this.timedata[0];
        enddate = this.timedata[1];
      }
      var formData = {
        startdate: startdate,
        enddate: enddate,
        attrname: this.sdeviceattr,
        deviceid: this.sdevicename,
      }
      vibrate_chart(formData, res => {
        var datalist = res.data
        var listpush = []
        var listpush2 = []
        datalist.map(item => {
          listpush.push([item.acquisitiontime, item[this.sdeviceattr]])
          // listpush2.push([item.acquisitiontime, item.humidity])
        })
        this.edata = listpush
      });
    },
    // 获取设备类型信息
    // getdevicetype() {
    //   devicetype({}, res => {
    //     this.devicetype = res.data
    //   });
    // },
    // 根据设备类型获取设备名称
    getdevicename() {
      var formData = {
        devicetype: '021086',
      }
      devicename(formData, res => {
        this.devicename = res.data
      });
    },
    // 获取设备属性
    getdeviceattr() {
      var formData = {
        devicetype: '021086',
      }
      deviceattr(formData, res => {
        this.deviceattr = res.data
      });
    },
  }
};



